import React from 'react';
import PropTypes from 'prop-types';
import FormComponent from './FormComponent';
import { Link } from 'react-router-dom';


const TableRow = ({
    property: { id, unit_number, property_type, category, location, bedrooms, bathrooms, furnished_type, listing_price, title, description }
}) => (
        <tr>
            <td>{id}</td>
            <td>{unit_number}</td>
            <td>{property_type}</td>
            <td>{category}</td>
            <td>{location}</td>
            <td>{bedrooms}</td>
            <td>{bathrooms}</td>
            <td>{listing_price}</td>
            <td> <Link to={`/view/${id}`}>VIEW</Link></td>
            <td><Link to={`/edit/${id}`}>Edit</Link></td>


        </tr >

    );

TableRow.propTypes = {
    property: PropTypes.object.isRequired
};

export default TableRow;
