import React, { Fragment, useEffect } from 'react';
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import { Provider } from 'react-redux';
import store from './store';

import NavBar from './components/Layout/NavigationBar';
import ViewFormComponent from './components/form/ViewFormComponent';
import EditFormComponent from './components/form/EditFormComponent';
import FormComponent from './components/form/FormComponent';
import TableComponent from './components/form/TableComponent';
import Alert from './components/form/Alert';
import Header from './components/Layout/Header';
function App() {

  return (
    <div className='root'>


      <Provider store={store}>
        <Router>
          <Fragment>
            <NavBar />
            <Header />
            <Alert />
            <Switch>

              <Route exact path="/edit/:id" component={EditFormComponent} />
              <Route exact path="/view/:id" component={ViewFormComponent} />
              <Route exact path="/" component={FormComponent} />
              <Redirect to='/' />
            </Switch>

            <TableComponent />
          </Fragment>
        </Router>
      </Provider>
    </div>
  );
}

export default App;
