import {
	ADD_DATA_SUCCESS,
	GET_DATA_SUCCESS
} from '../actions/types';

const initialState = {
};

export default function (state = initialState, action) {
	const { type, payload } = action;

	switch (type) {
		case ADD_DATA_SUCCESS:
			return {
				...state,
				reload: true
			};
		case GET_DATA_SUCCESS:
			return {
				...state,
				propertiesData: payload.data,

			};
		default:
			return state;
	}
}
