import axios from 'axios';
import { setAlert } from './alert';
import {
  ADD_DATA_SUCCESS,
  GET_DATA_SUCCESS
} from './types';


export const addData = ({ id, unit_number, property_type, category, location, bedrooms, bathrooms, furnished_type, listing_price, title, description }, history) => async dispatch => {
  const config = {
    headers: {
      'Content-Type': 'application/json'
    }
  };
  const data = JSON.stringify({ id, unit_number, property_type, category, location, bedrooms, bathrooms, furnished_type, listing_price, title, description });
  try {

    const res = await axios.post('/add', data, config);
    console.log(res.data);
    console.log(res.data.message);
    if (res.data.status === 11000) {
      dispatch(setAlert(res.data.message, 'danger'));
    } else if (res.data.status === 202) {
      dispatch(setAlert(res.data.message, 'danger'));
    } else {
      dispatch({
        type: ADD_DATA_SUCCESS,
        payload: res.data
      });
      dispatch(setAlert('Data Added', 'success'));
      getData();
      history.push('/vi');
    }

  } catch (err) {
    const errors = err.data;

    if (errors) {
      errors.forEach(error => dispatch(setAlert(error.msg, 'danger')));
    }

  }
};

export const getData = () => async dispatch => {
  try {
    const res = await axios.get('/get');
    console.log(res);
    dispatch({
      type: GET_DATA_SUCCESS,
      payload: res.data
    });
  } catch (err) {
    const errors = err.response.data.errors;

    if (errors) {
      errors.forEach(error => dispatch(setAlert(error.msg, 'danger')));
    }

  }
};


