import React, { Fragment, useState, useEffect } from 'react';
import { Table } from 'reactstrap';
import { connect } from 'react-redux';
import { getData } from '../../actions/form';
import PropTypes from 'prop-types';
import Spinner from '../Spinner/Spinner';
import TableRow from './TableRow';

const TableComponent = ({
    getData, propertiesData
}) => {
    useEffect(() => {
        getData();
    }, [getData]);

    return (
        <Fragment>
            {propertiesData === null || propertiesData === undefined ? (
                <Spinner />
            ) : (
                    <Fragment>
                        <div className='container'>
                            <div className='row'>
                                <div className='col-12'>
                                    {/* <h3>List</h3> */}
                                </div>
                            </div>
                            <div className='row mt-5'>
                                <div className='col-12'>
                                    <div class="table-wrapper-scroll-y my-custom-scrollbar">

                                        <Table className="table-striped  table-hover table tableFixHead ">
                                            <thead className='thead-dark'>
                                                <tr>
                                                    <th>id</th>
                                                    <th>Unit Number</th>
                                                    <th>Property Type</th>
                                                    <th>Category</th>
                                                    <th>Location</th>
                                                    <th>Bedrooms</th>
                                                    <th>bathrooms</th>
                                                    <th>Listing Price</th>
                                                    <th colSpan="2">Action</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {propertiesData.map(data => (
                                                    <TableRow
                                                        key={data._id}
                                                        property={data}

                                                    />
                                                ))}
                                            </tbody>
                                        </Table>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </Fragment>
                )}
        </Fragment>
    );

};

TableComponent.propTypes = {
    getData: PropTypes.func.isRequired,
    propertiesData: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
    propertiesData: state.form.propertiesData
});

export default connect(mapStateToProps, { getData })(TableComponent);
