import React, { Fragment, useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import { Col, Row, Button, Form, FormGroup, Label, Input, CustomInput } from 'reactstrap';
import { connect } from 'react-redux';
import { updateData, getData } from '../../actions/form';
import PropTypes from 'prop-types';
import store from '../../store';
import { Link } from 'react-router-dom';
var initialState = {
  id: '',
  unit_number: '',
  property_type: '',
  category: '',
  location: '',
  bedrooms: '',
  bathrooms: '',
  furnished_type: '',
  listing_price: '',
  title: '',
  description: '',
};

const FormComponent = ({
  updateData, getData, history, match
}) => {

  const state = store.getState();
  //alert((match.params.id));
  const data = state.form.propertiesData.filter((data) => data.id === match.params.id)[0];
  console.log(data);
  const [formData, setFormData] = useState({ ...data });
  useEffect(() => { setFormData(data) }, [data]);
  const { id, unit_number, property_type, category, location, bedrooms, bathrooms, furnished_type, listing_price, title, description } = formData;
  const onChange = e =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = e => {
    e.preventDefault();
    //alert('hello');
    updateData({ id, unit_number, property_type, category, location, bedrooms, bathrooms, furnished_type, listing_price, title, description }, history);
    getData();
  };
  const onClear = e => {
    e.preventDefault();
    setFormData(initialState);
  };

  return (
    <Fragment>
      <div className='container'>
        <div className='row mt-3'>
          <div className='col-12'>
            <div className="card bg-secondary text-white">
              <div className="card-header">
                <h3 className="mb-0">Listing Details</h3>
              </div>
              <div className="card-body">
                <Form onSubmit={e => onSubmit(e)}>
                  <Row form>
                    <Col md={4}>
                      <FormGroup>
                        <Label for="exampleID">ID</Label>
                        <Input type="text" className="input" required name="id" value={id} onChange={e => onChange(e)} id="exampleID" />

                      </FormGroup>
                    </Col>
                    <Col md={4}>
                      <FormGroup>
                        <Label for="examplePassword">Title</Label>
                        <Input type="text" name="title" required value={title} onChange={e => onChange(e)} id="examplePassword" />
                      </FormGroup>
                    </Col>
                    <Col md={4}>
                      <FormGroup>
                        <Label for="exampleNumber">Unit Number</Label>
                        <Input type="text" name="unit_number" required min='1' value={unit_number} onChange={e => onChange(e)} id="exampleNumber" />
                      </FormGroup>

                    </Col>

                  </Row>
                  <Row form>
                    <Col md={4}>
                      <FormGroup>
                        <Label for="exampleBedrooms">Bedrooms</Label>
                        <Input type="number" name="bedrooms" required min='1' value={bedrooms} onChange={e => onChange(e)} id="exampleBedrooms" />
                      </FormGroup>
                    </Col>
                    <Col md={4}>
                      <FormGroup>
                        <Label for="examplebathrooms">bathrooms</Label>
                        <Input type="number" name="bathrooms" required min='1' value={bathrooms} onChange={e => onChange(e)} id="examplebathrooms" />
                      </FormGroup>
                    </Col>
                    <Col>
                      <FormGroup>
                        <Label for="exampleCustomSelect">Category</Label>
                        <CustomInput type="select" id="exampleCustomSelect" required name="category" value={category} onChange={e => onChange(e)}>
                          <option value="">Select</option>
                          <option>Owned</option>
                          <option>Leased</option>
                          <option>rented</option>
                        </CustomInput>
                      </FormGroup>
                    </Col>

                  </Row>

                  <Row form>
                    <Col md={4}>
                      <FormGroup>
                        <Label for="exampleCustomSelect">Property Type</Label>
                        <CustomInput type="select" id="exampleCustomSelect" required name="property_type" value={property_type} onChange={e => onChange(e)}>
                          <option value="">Select</option>
                          <option>Flat</option>
                          <option>House</option>
                          <option>Plots</option>
                        </CustomInput>
                      </FormGroup>
                    </Col>

                    <Col md={4}>
                      <FormGroup>
                        <Label for="examplePrice">Listing Price</Label>
                        <Input type="number" name="listing_price" required min='1' value={listing_price} onChange={e => onChange(e)} id="examplePrice" />
                      </FormGroup>
                    </Col>
                    <Col md={4}>
                      <FormGroup>
                        <Label for="exampleLocation">Location</Label>
                        <Input type="text" name="location" required value={location} onChange={e => onChange(e)} id="exampleexampleLocation" />
                      </FormGroup>

                    </Col>

                  </Row>
                  <Row form>
                    <Col md={2} className='mt-3'>
                      <CustomInput type="radio" id="exampleCustomRadio1" name="furnished_type" required value={furnished_type} onChange={e => onChange(e)} label="Furnished" />
                      <CustomInput type="radio" id="exampleCustomRadio2" name="furnished_type" required value={furnished_type} onChange={e => onChange(e)} label="Semi Furnished" />

                    </Col>
                    <Col md={2} className='mt-3'>
                      <CustomInput type="radio" id="exampleCustomRadio3" name="furnished_type" required value={furnished_type} onChange={e => onChange(e)} label="Unfurnished" />
                      <CustomInput type="radio" id="exampleCustomRadio4" name="furnished_type" required value={furnished_type} onChange={e => onChange(e)} label="Upgraded" />

                    </Col>
                    <Col md={8}>
                      <FormGroup>
                        <Label for="exampleDescription">Description</Label>
                        <Input type="textarea" name="description" required value={description} onChange={e => onChange(e)} id="exampleDescription" />
                      </FormGroup>
                    </Col>

                  </Row>

                  <Row>

                  </Row>
                  <Row>

                    <Col className='col-1 offset-md-10'>
                      <div className="btn-group" >
                        <Button className="btn-lg" color="danger" onClick={() => history.push('/')}>Clear</Button>
                        <Button color="success" type='submit'>Update</Button>
                      </div>
                    </Col>

                  </Row>

                </Form>
              </div>
            </div>

          </div>
        </div>
      </div>

    </Fragment>
  );
};

FormComponent.propTypes = {
  updateData: PropTypes.func.isRequired,
  match: PropTypes.object.isRequired,
};

export default connect(null, { updateData, getData })(withRouter(FormComponent));
